<template>
  <div>
    <Header name="我的售后"></Header>
    <div style="background: #f7f7f7;" :style="{'min-height' : (data && data.length > 0 ? '750px':'750px')}">
      <van-tabs v-model="active" @click="showOrder(active)">
        <van-tab title="退货" name="1">
          <div v-if="data && data.length > 0" style="margin-top:10px">
            <div class="orderBody" v-for="item in data" :key="item.id">
          <div class="orderTop">
            <p>
              订单号：<span>{{ item.orderSn }}</span>
            </p>
            <p>{{item.createTime}}</p>
          </div>
          <div
            class="orderCenter"
            v-for="itm in item.orderItemList"
            :key="itm.id"
          >
            <div class="orderimg">
              <van-image :src="itm.productPic ? itm.productPic : ''"></van-image>
            </div>
            <div class="orderfont">
              <div class="ttop">
                {{ itm.productName }}
              </div>
              <div class="shopSpecs" v-if="itm.productAttr">
                <div v-for="(ite,i) in JSON.parse(itm.productAttr || `[]`)" :key="i">{{ite.value}}</div>
              </div>
              <div class="bbottom">
                <div class="left" v-if="item.payType === 5">
                  <span>{{ itm.productPrice }}<span style="font-size:14px;margin:0">积分</span></span>x{{ itm.productQuantity }}
                </div>
                <div class="left" v-else>
                  <span>￥{{ itm.productPrice }}</span>x{{ itm.productQuantity }}
                </div>
                <div @click="lookOver(item.orderSn,itm.id,item.returnApplyList,itm.orderId)">查看详情</div>
              </div>
            </div>
          </div>
          <div class="orderBottom">
            <div class="money">
              <span>退款金额￥{{ item.totalAmount }}</span>
            </div>
            <div class="btn">
            </div>
          </div>
            </div>
            <div style="height: 10px"></div>
          </div>
          <div v-if="isOk">
        <van-empty
          class="custom-image"
          image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
          description="暂无数据"
        />
          </div>
        <van-loading size="24px" vertical type="spinner" v-if="!isOk && data.length === 0">加载中...</van-loading>
        </van-tab>
        <van-tab title="退款" name="2">
          <div v-if="data && data.length > 0" style="margin-top:10px">
            <div class="orderBody" v-for="item in data" :key="item.id">
          <div class="orderTop">
            <p>
              订单号：<span>{{ item.orderSn }}</span>
            </p>
            <p>{{item.createTime}}</p>
          </div>
          <div
            class="orderCenter"
            v-for="itm in item.orderItemList"
            :key="itm.id"
          >
            <div class="orderimg">
              <van-image :src="itm.productPic ? itm.productPic : ''"></van-image>
            </div>
            <div class="orderfont">
              <div class="ttop">
                {{ itm.productName }}
              </div>
              <div class="shopSpecs" v-if="itm.productAttr">
                <div v-for="(ite,i) in JSON.parse(itm.productAttr || `[]`)" :key="i">{{ite.value}}</div>
              </div>
              <div class="bbottom">
                <div class="left" v-if="item.payType === 5">
                  <span>{{ itm.productPrice }}<span style="font-size:14px;margin:0">积分</span></span>x{{ itm.productQuantity }}
                </div>
                <div class="left" v-else>
                  <span>￥{{ itm.productPrice }}</span>x{{ itm.productQuantity }}
                </div>
                <div @click="lookOver(item.orderSn,itm.id,item.returnApplyList,itm.orderId)">查看详情</div>
              </div>
            </div>
          </div>
          <div class="orderBottom">
            <div class="money">
              <span>退款金额￥{{ item.totalAmount }}</span>
            </div>
            <div class="btn">
            </div>
          </div>
            </div>
            <div style="height: 10px"></div>
          </div>
          <div v-if="isOk">
            <van-empty
              class="custom-image"
              image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
              description="暂无数据"
            />
          </div>
        <van-loading size="24px" vertical type="spinner" v-if="!isOk && data.length === 0">加载中...</van-loading>
        </van-tab>
        <van-tab title="换货" name="3">
          <div v-if="data && data.length > 0" style="margin-top:10px">
            <div class="orderBody" v-for="item in data" :key="item.id">
          <div class="orderTop">
            <p>
              订单号：<span>{{ item.orderSn }}</span>
            </p>
            <p>{{item.createTime}}</p>
          </div>
          <div
            class="orderCenter"
            v-for="itm in item.orderItemList"
            :key="itm.id"
          >
            <div class="orderimg">
              <van-image :src="itm.productPic ? itm.productPic : ''"></van-image>
            </div>
            <div class="orderfont">
              <div class="ttop">
                {{ itm.productName }}
              </div>
              <div class="shopSpecs" v-if="itm.productAttr">
                <div v-for="(ite,i) in JSON.parse(itm.productAttr || `[]`)" :key="i">{{ite.value}}</div>
              </div>
              <div class="bbottom">
                <div class="left" v-if="item.payType === 5">
                  <span>{{ itm.productPrice }}<span style="font-size:14px;margin:0">积分</span></span>x{{ itm.productQuantity }}
                </div>
                <div class="left" v-else>
                  <span>￥{{ itm.productPrice }}</span>x{{ itm.productQuantity }}
                </div>
                <div @click="lookOver(item.orderSn,itm.id,item.returnApplyList,itm.orderId)">查看详情</div>
              </div>
            </div>
          </div>
          <div class="orderBottom">
            <div class="money">
              <span>订单金额￥{{ item.payAmount }}</span>
            </div>
            <div class="btn">
            </div>
          </div>
            </div>
            <div style="height: 10px"></div>
          </div>
          <div v-if="isOk">
            <van-empty
              class="custom-image"
              image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
              description="暂无数据"
            />
          </div>
        <van-loading size="24px" vertical type="spinner" v-if="!isOk && data.length === 0">加载中...</van-loading>
        </van-tab>
      </van-tabs>
      <div class="kefu" @click="toKefu">
        <img :src="require('@/assets/image/kefu.png')" alt="">
        <p>客服</p>
      </div>
    </div>
  </div>
</template>

<script>
import * as api from "@/data/api/market/index"
import Header from '@/components/Layout/header/index'
export default {
  components :{
    Header
  },
  data() {
    return {
      active:'1',
      isOk:false,
      data: [],
      form: {
        pageNum: 1,
        pageSize: 100,
      },
    };
  },
  mounted() {
    this.showOrder("1");
  },
  methods: {
    toKefu(){
      window.location.href = 'https://work.weixin.qq.com/kfid/kfcd64beafe27d5326e'
    },
    showOrder(type) {
      let me = this;
      me.data = []
      me.isOk = false
      if(type ===  '1'){
        api.returnList(me.form).then((res) => {
          if (res.code === 200) {
            if (res.data.list) {
              res.data.list.forEach(item => {
                let arr = []
                let newAmount = 0
                let returnArr = []
                item.returnApplyList.forEach((itm) => {
                  if(itm.type === 1) {
                    returnArr.push(itm)
                  }
                })
                returnArr.forEach((itmm, ind, listArr) => {
                  if(listArr.length > 1) {
                    if(itmm.status >= 3) {
                      listArr.splice(ind, 1)
                    }
                  }
                })
                item.returnApplyList = returnArr
                item.returnApplyList.forEach((itm) => {
                  item.orderItemList.forEach((term) => {
                    if(itm.orderItemId) {
                      if(term.id === itm.orderItemId && itm.type === 1){
                        newAmount += itm.returnAmount
                        arr.push(term)
                      }
                    } else {
                      newAmount += itm.returnAmount
                      arr.push(term)
                    }
                  })
                })
                item.orderItemList = arr
                item.totalAmount = newAmount
              })
              me.data = res.data.list
            } else {
              me.data = [];
              me.isOk = true
            }
          } else {
            me.$message.error(res.message);
          }
        })
        .catch((res) => {
          me.$message.error(res.message);
        })
        .finally(() => {});
      }else if(type ===  '2'){
        api.listRefund(me.form).then((res) => {
          if (res.code === 200) {
            if (res.data.list) {
              res.data.list.forEach((item) => {
                let arr = []
                let newAmount = 0
                // itm.orderItemList.forEach((term, index, arr) => {
                //   itm.returnApplyList.forEach((item) => {
                //     if(Number(term.id) != Number(item.orderItemId) && arr.length > 1){
                //       itm.payAmount = item.orderItemPay
                //       arr.splice(index, 1)
                //     }
                //   })
                // })
                let returnArr = []
                item.returnApplyList.forEach((itm) => {
                  if(itm.type === 2) {
                    returnArr.push(itm)
                  }
                })
                returnArr.forEach((itmm, ind, listArr) => {
                  if(listArr.length > 1) {
                    if(itmm.status >= 3) {
                      listArr.splice(ind, 1)
                    }
                  }
                })
                item.returnApplyList = returnArr
                item.returnApplyList.forEach((itm) => {
                  item.orderItemList.forEach((term) => {
                    if(itm.orderItemId) {
                      if(term.id === itm.orderItemId && itm.type === 2){
                        newAmount += itm.returnAmount
                        arr.push(term)
                      }
                    } else {
                      newAmount = itm.returnAmount
                      arr.push(term)
                    }
                  })
                })
                item.orderItemList = arr
                item.totalAmount = newAmount
              })
              me.data = res.data.list
            } else {
              me.data = [];
              me.isOk = true
            }
          } else {
            me.$message.error(res.message);
          }
        })
        .catch((res) => {
          me.$message.error(res.message);
        })
        .finally(() => {});
      }else{
        api.listExchange(me.form).then((res) => {
          if (res.code === 200) {
            if (res.data.list) {
              res.data.list.forEach(item => {
                let arr = []
                let returnArr = []
                item.returnApplyList.forEach((itm) => {
                  if(itm.type === 3) {
                    returnArr.push(itm)
                  }
                })
                returnArr.forEach((itmm, ind, listArr) => {
                  if(listArr.length > 1) {
                    if(itmm.status >= 3) {
                      listArr.splice(ind, 1)
                    }
                  }
                })
                item.returnApplyList = returnArr
                item.returnApplyList.forEach((itm) => {
                  item.orderItemList.forEach((term) => {
                    if(itm.orderItemId) {
                      if(term.id === itm.orderItemId && itm.type === 3){
                        arr.push(term)
                      }
                    } else {
                      arr.push(term)
                    }
                  })
                })
                item.orderItemList = arr
              })
              me.data = res.data.list
            } else {
              me.data = [];
              me.isOk = true
            }
          } else {
            me.$message.error(res.message);
          }
        })
        .catch((res) => {
          me.$message.error(res.message);
        })
        .finally(() => {})
      }
    },
    lookOver(id,fid,type,ffid) {
      type.forEach(item => {
        if(item.status !== 3){
          if(item.type === 1){
            this.$router.push({
              path: "/user/returnGoods",
              query:{
                  orderSn:id,
                  id: ffid,
                  ffid:fid,
                  type:item.type
              }
            })
          } else if (item.type === 2) {
            this.$router.push({
              path: "/user/returnRefund",
              query:{
                  orderSn:id,
                  id: ffid,
                  ffid:fid,
                  type:item.type
              }
            })
          } else if (item.type === 3) {
            this.$router.push({
              path: "/user/returnExchange",
              query:{
                  orderSn:id,
                  id: fid,
              }
            })
          }
        }
      })
    },
  },
};
</script>

<style lang="stylus" scoped>
>>>.van-loading--vertical{
  position relative
  top calc(50% - 23px)
}
.kefu{
  position fixed
  right: 0px;
  bottom: 160px;
  width: 61px;
  height: 50px;
  background: #fff
  border-radius: 25px 0px 0px 25px
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 6px #CDCDCD
  img{
    width: 22px;
    height: 22px;
    margin-left: 6px
  }
  p{
    margin-left: 6px
    margin-top: 5px
    font-size: 12px;
  }
}
.orderBody {
  width: 94.6%; // 355
  margin: 0px 2.6% 10px;
  padding-bottom: 20px;
  background: #fff;
  border-radius: 12px;

  .orderTop {
    width: 91.6%;
    height: 50px;
    margin: 0px 4.2%;
    border-bottom: 1px solid #f3f3f3;
    display: flex;
    justify-content: space-between;
    align-items: center;

    P {
      font-size: 14px;
      color: #666666;

      span {
        color: #000;
        font-weight: bold;
      }
    }
  }

  .orderCenter {
    width: 91.6%; // 325
    min-height: 86px;
    margin: 20px 4.2%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .orderimg {
      width: 26.4%;
      height: 86px;
      text-align: center;

      >>>.van-image {
        width: 86px;
        height: 86px;
      }
    }

    .orderfont {
      width: 69.2%;
      margin: 3px 0px;
      min-height: 80px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .ttop {
        overflow: hidden;
        // height: 40px;
        font-size: 15px;
        font-weight: bold;
        color: #000000;
        line-height: 21px;
      }
      .shopSpecs {
        display: flex;
        flex-wrap: wrap;
        font-size: 13px;
        margin-bottom: 5px;
        font-weight: 400;
        color: #888888;

        div{
          margin-right: 10px;
          margin-bottom: 5px;
        }
        }

      .bbottom {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left {
          font-size: 15px;

          span {
            color: #FD2744;
            font-size: 18px;
            font-weight: bold;
            margin-right: 10px;
          }
        }

        div {
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }

  .orderBottom {
    width: 91.6%;
    margin: 20px 4.2% 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;

    .money {
      font-size: 13px;
      color: #999;

      span {
        font-weight: bold;
        font-size: 15px;
        color: #000;
      }
    }

    .btn {
      margin-top: 16px;
    }
  }
}
</style>


